import React, { useState } from "react";
import RecipeDetails from "./RecipeDetails";
import Link from "@material-ui/core/Link";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import RestaurantMenuOutlinedIcon from "@material-ui/icons/RestaurantMenuOutlined";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "30rem",
    margin: "1rem",
    display: "flex",
    flexDirection: "column",
    padding: "2rem",
    position: "relative",
    backgroundColor: "bisque",
  },
  media: {
    height: 0,
    paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  },
}));

function LandingPageBody({ recipe }) {
  const classes = useStyles();
  const [expanded, setExpanded] = useState(false);
  const { label, image, ingredients, url, source, dietLabels } = recipe.recipe;
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            <RestaurantMenuOutlinedIcon />
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={label}
      />
      <CardMedia className={classes.media} image={image} title={label} />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          DietLabel as:<strong>{dietLabels | "NA"}</strong> . By{" "}
          <strong>{source}'s</strong>.<Link href={url}>Link here</Link>
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>

      <Collapse in={expanded} timeout="auto" unmountOnExit collapsedHeight={20}>
        <CardContent>
          <Typography paragraph>Ingredients:</Typography>
          {expanded && <RecipeDetails ingredients={ingredients} />}
        </CardContent>
      </Collapse>
    </Card>
  );
}

export default LandingPageBody;
